.app-wrapper {
  width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-areas: "h h" "n c";

    grid-template-rows: 70px 1fr;
    grid-template-columns: 2fr 10fr;


}

body {
    background-color: #FAF8F3;
}
.app-wrapper-content {
  grid-area: c;
  background-color: white;
  position: relative;
}